.page {
  min-width: 320px;
  max-width: 450px;
  margin: 0 auto;
  box-sizing: border-box;
  font-family: Oswald, "Arial Narrow", sans-serif;
  font-weight: 400;
  font-style: normal;
  font-size: 14px;

  @media(min-width: @width-tablet) {
    min-width: @width-tablet;
    max-width: 1000px;
    font-size: 16px;
  }

  @media(min-width: @width-desktop) {
    min-width: @width-desktop;
    max-width: 1600px;
  }
}
