.promo {
  position: relative;
  width: 100%;
  // height: 480px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 27px;
  background-color: @color-green;
  background: url(../img/index-bg-mobile.webp) top center no-repeat;
  background-size: contain;
  box-sizing: border-box;

  @media(min-width: @width-tablet) {
    position: relative;
    min-width: 709px;
    padding-left: 30px;
    padding-right: 30px;
    padding-top: 64px;
    background-color: transparent;
    background: none;
  }

  @media(min-width: @width-desktop) {
    width: 100%;
    padding-left: 110px;
    padding-right: 110px;
    padding-top: 118px;
    background-color: transparent;

    &::after {
      content:"";
      position: absolute;
      top: -114px;
      right: 0;
      width: 720px;
      height: 696px;
      background: @color-green url(../img/index-bg-desktop.webp) no-repeat;
      z-index: -10;
    }

  }
}

@media
  (min-resolution: 144dpi),
  (min-resolution: 1.5dppx)  {
  .promo {
    background-image: url(../img/index-bg-mobile@2x.webp);
    background-size: contain;

    @media(min-width: @width-tablet) {
      background-image: none;
    }

    @media(min-width: @width-desktop) {
      &::after{
        background-image: url(../img/index-bg-desktop@2x.webp);
        background-size: 720px;
      }


    }
  }
}

.promo__text-block {
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 29px;
  text-align: center;

  @media(min-width: @width-tablet) {
    width: 420px;
    margin-bottom: 49px;
    margin-left: 87px;
    text-align: left;
  }

  @media(min-width: @width-desktop) {
    margin-left: 60px;
    margin-bottom: 52px;
  }
}

.promo__title {

  @media(min-width: @width-tablet) {
    text-align: left;
  }
}

.promo__slogan {
  margin: 0;
  margin-top: 25px;
  color: @color-white;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;

  @media(min-width: @width-tablet) {
    margin-top: 41px;
    color: @color-black;
    font-size: 20px;
    line-height: 20px;
  }

  @media(min-width: @width-desktop) {
    margin-top: 40px;
    margin-bottom: 52px;
    margin-left: 20px;
  }
}

.promo__image-block {
  padding-left: 20px;
  padding-right: 20px;

  @media(min-width: @width-tablet) {
    position: absolute;
    top: 263px;
    right: 30px;
    z-index: -5;
    width: 709px;
    height: 609px;
  }

  @media(min-width: @width-desktop) {
    top: 32px;
    right: 330px;
    width: 552px;
    height: 449px;

  }
}

.promo__link {
  width: 280px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 3px;
  padding-top: 10px;
  padding-bottom: 10px;
  padding-right: 44px;
  padding-left: 44px;
  color: @color-white;
  font-size: 14px;
  line-height: 18px;
  background-color: @color-green;

  @media(min-width: @width-tablet) {
    width: 244px;
    height: auto;
    margin-left: 87px;
    padding-top: 14px;
    padding-bottom: 14px;
    padding-right: 26px;
    padding-left: 26px;
    font-size: 20px;
    line-height:20px;
  }

  // @media(min-width: @width-desktop) {
  //   top: 343px;
  //   left: 190px;
  // }

}
