.btn-menu {
  font-size: 0;
  width: 24px;
  height: 16px;
  padding: 0px;
  background-image: url("../img/btn-menu.svg");
  background-size: contain;
  background-color: transparent;
  background-repeat: no-repeat;
  border: 0;
  outline: transparent;

  &--opened {
    width: 18px;
    height: 18px;
    background-image: url(../img/btn-close.svg);
    background-size: 18px;
  }
}



