.offer {
  padding-left: 20px;
  padding-right: 20px;

  @media(min-width: @width-tablet) {
    margin-top: 526px;
    padding-left: 30px;
    padding-right: 30px;
  }

  @media(min-width: @width-desktop) {
    display: flex;
    justify-content: space-between;
    margin-top: 265px;
    padding-left: 110px;
    padding-right: 110px;
  }
}

.offer__item {
  position: relative;
  margin-top: 20px;
  padding-top: 30px;
  padding-right: 30px;
  padding-bottom: 18px;
  padding-left: 20px;
  background: #F2F2F2;

  @media(min-width: @width-tablet) {
    padding-top: 77px;
    padding-bottom: 53px;
    padding-left: 52px;
  }

  @media(min-width: @width-desktop) {
    width: 570px;
    padding-top: 77px;
    padding-right: 51px;
    padding-bottom: 55px;
    padding-left: 52px;
    box-sizing: border-box;
  }
}

.offer-title {
  position: relative;
  margin: 0;
  margin-bottom: 29px;
  padding-left: 71px;
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  text-transform: uppercase;

  @media(min-width: @width-tablet) {
    margin-bottom: 24px;
    padding-left: 0;
    font-size: 36px;
    line-height: 36px;
    text-align: left;
  }

  @media(min-width: @width-tablet) {
    margin-bottom: 73px;
    padding-left: 162px;
  }
}

.offer__text {
  margin: 0;
  margin-bottom: 23px;
  font-size: 14px;
  line-height: 18px;

  @media(min-width: @width-tablet) {
    margin-bottom: 48px;
    font-size: 16px;
    line-height: 24px;
    text-align: left;
  }

  @media(min-width: @width-desktop) {
    margin-bottom: 23px;
  }
}

.offer__losing-weight {

  @media(min-width: @width-tablet) {
    padding-right: 279px;
  }

  @media(min-width: @width-desktop) {
    padding-right: 50px;
  }

  &::after {
    content:"";
    position: absolute;
    top: 21px;
    left: 22px;
    width: 50px;
    height: 53px;
    background-image: url(../img/cat-back.svg);
    background-size: 50px;
    background-repeat: no-repeat;

    @media(min-width: @width-tablet) {
      top: 81px;
      left: auto;
      right: 62px;
      width: 200px;
      height: 211px;
      background-size: 200px;
    }

    @media(min-width: @width-desktop) {
      top: 41px;
      left: 52px;
      right: auto;
      width: 100px;
      height: 106px;
      background-size: 100px;
    }
  }
}

.offer__set-weight {

  @media(min-width: @width-tablet) {
    padding-right: 314px;
  }

  @media(min-width: @width-desktop) {
    padding-right: 60px;
  }
}

.offer__set-weight::after {
  content:"";
  position: absolute;
  top: 24px;
  left: 13px;
  width: 67px;
  height: 50px;
  background-image: url(../img/cat-back2.svg);
  background-size: 67px;
  background-repeat: no-repeat;

  @media(min-width: @width-tablet) {
    top: 91px;
    left: auto;
    right: 28px;
    width: 268px;
    height: 200px;
    background-size: 268px;
  }

  @media(min-width: @width-desktop) {
    top: 47px;
    left: 33px;
    right: auto;
    width: 134px;
    height: 100px;
    background-size: 134px;
  }
}

.offer__link {
  position: relative;
  width: 100%;
  color: @color-black;
  padding-top: 19px;
  padding-right: 47px;
  font-size: 16px;
  line-height: 16px;
  text-align: left;
  border-top: 1px solid #D9D9D9;

  @media(min-width: @width-tablet) {
    padding-top: 0;
    padding-right: 57px;
    font-size: 20px;
    line-height: 30px;
    border-top: none;
  }

  @media(min-width: @width-desktop) {
    padding-right: 60px;
  }

  &::after {
    content:"";
    position: absolute;
    top: 22px;
    left: 102px;
    width: 32px;
    height: 10px;
    background-image: url(../img/arrow.svg);
    background-repeat: no-repeat;

    @media(min-width: @width-tablet) {
      top: 10px;
      left: 133px;
    }

    @media(min-width: @width-desktop) {
      left: 136px;
    }
  }
}
