.header {
  position: relative;

  @media(min-width: @width-tablet) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 24px;
    padding-right: 30px;
    padding-left: 30px;
  }

  @media(min-width: @width-desktop) {
    padding-top: 53px;
    padding-right: 110px;
    padding-left: 110px;
  }
}

.header__logo {
  width: 100%;
  padding-top: 12px;
  padding-right: 20px;
  padding-bottom: 16.5px;
  padding-left: 20px;
  border-bottom: 1px solid #E6E6E6;
  box-sizing: border-box;

  @media(min-width: @width-tablet) {
    width: 174px;
    padding: 0;
    border-bottom: 0;
  }

  @media(min-width: @width-desktop) {
    width: 202px;
  }
}
