.menu {
  width: 100%;

  &--closed {
    display: none;
  }

  @media(min-width: @width-tablet) {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 440px;
    height: 36px;
  }

  @media(min-width: @width-desktop) {
    width: 478px;
    height: 40px;
  }
}

.menu__link {
  width: 100%;
  padding-top: 22px;
  padding-bottom: 24px;
  color: @color-black;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
  border-bottom: 1px solid #E6E6E6;

  @media(min-width: @width-tablet) {
    font-size: 18px;
    font-weight: 400;
    padding-top: 0px;
    padding-bottom: 8px;
    border-bottom: 0;
  }

  @media(min-width: @width-desktop) {
    color: @color-white;
  }

    &:hover {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
}

.menu__link--active {
  @media(min-width: @width-tablet) {
    border-bottom: 2px solid #68B738;
  }


  @media(min-width: @width-desktop) {
    border-bottom-color: @color-white;
  }

  &:hover {
    opacity: 1;
    cursor: default;
  }
}



